export default {
	data() {
		return {
			type:0,
			list:[
				{name:'全部'},
				{name:'代付款'},
				{name:'待收货'},
				{name:'待评价'},
				{name:'售后'},
			]
		}
	},
	methods: {
		select(index){
			this.type=index
		},
		moveLine(){
			let view = uni.createSelectorQuery().in(this).select('#type'+this.type)
			console.log('obj',view)
				view.fields({
				    dataset: true,
				    size: true,
				    scrollOffset: true,
				    properties: ['scrollX', 'scrollY']
				}, res => {
			console.log('res',res)
				}).exec()
		}
	},
	mounted() {
		this.moveLine()
	}
}
